<!-- 学员预约 -->
<template>
	<view>
		<view class="ul" v-if="list.length!=0">
			<view class="li" v-for="(k,i) in list" :key="k.id">
				<view class="up">
					<view class="left">
						<view class="msg" style="margin-bottom: 20rpx;">
							<text>{{k.mpName}}</text>
							<text>{{k.phone}}</text>
						</view>
						<view class="time">
							<text>练车时间：{{k.carTime}}</text>
						</view>
					</view>
					<view class="right">
						{{k.carTypes}}
					</view>
				</view>
				<view class="down">
					<view class="left">
						<text class="address" style="margin-bottom: 16rpx;">接送地址：梅州市梅江区华侨城新中路2号</text>
						<!-- <text class="time">接送时间：2019/06/12 15:00</text> -->
					</view>
					<view class="right">
						<image :src="cdn+'/image/venue/locationIcon.png'" mode="aspectFill"></image>
					</view>
				</view>
			</view>
		</view>
		<view class="noBooking" v-else>
			<image :src="cdn+'/image/noList/noBooking.png'" mode="aspectFill"></image>
			<text>暂无预约</text>
		</view>
	</view>
</template>

<script>
	import {
		mapActions,
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				cdn: this.$conf.CDN_HOST,
				isShow: true,
			}
		},
		methods: {
			...mapActions({
				getBookingList_fun: 'coachSection/studentBooking'
			}),
			async getList() {
				let data = {
					userId: this.$db.getSync('userId'),
					page: 1

				}
				let res = await this.getBookingList_fun(data)

			}
		},
		computed: {
			...mapState({
				list: state => state.coachSection.bookingList
			})
		},
		onShow() {
			this.getList()
		}

	}
</script>

<style lang="scss">
	page {
		background-color: #f2f3f5;
	}

	.ul {
		// margin-top: 128rpx;
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 750rpx;
		box-sizing: border-box;
		padding: 15rpx 0;

		.li {
			display: flex;
			flex-direction: column;
			width: 690rpx;
			min-height: 249rpx;
			background: #ffffff;
			border-radius: 16rpx;
			margin: 15rpx 0;
			padding: 0 24rpx;
			box-sizing: border-box;

			.up {
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				width: 100%;
				padding: 30rpx 0;
				border-bottom: 2rpx solid #f5f5f5;

				.left {
					display: flex;
					flex-direction: column;

					.msg {
						font-size: 30rpx;
						font-family: Source Han Sans CN, Source Han Sans CN-Regular;
						font-weight: 400;
						text-align: left;
						color: #333333;
					}

					.time {
						font-size: 24rpx;
						font-family: Source Han Sans CN, Source Han Sans CN-Regular;
						font-weight: 400;
						text-align: left;
						color: #999999;
					}
				}

				.right {
					font-size: 28rpx;
					font-family: Source Han Sans CN, Source Han Sans CN-Regular;
					font-weight: 400;
					text-align: right;
					color: #333333;
				}
			}

			.down {
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				width: 100%;
				padding: 24rpx 0;

				.left {
					display: flex;
					flex-direction: column;

					text {
						font-size: 24rpx;
						font-family: Source Han Sans CN, Source Han Sans CN-Regular;
						font-weight: 400;
						text-align: left;
						color: #999999;
					}

				}

				.right {
					margin-right: 10rpx;

					image {
						// background-color: skyblue;
						width: 42rpx;
						height: 46rpx;
					}
				}
			}
		}
	}

	.noBooking {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin: 322rpx auto 0;

		image {
			// background-color: skyblue;
			margin-bottom: 30rpx;
			width: 326rpx;
			height: 163rpx;
		}

		text {
			font-size: 28rpx;
			font-family: Source Han Sans CN, Source Han Sans CN-Regular;
			font-weight: 400;
			text-align: center;
			color: #999999;
		}
	}
</style>
